    <div class="bg-grey cd_blocks margin-bottom-0" id="nps-story">
      <div>
        <Section :title="'新契约部服务故事分享'" :icon="'title_servise@2x.png'" v-bind:bg="'grey'">
          <div class="desc-text-box ">
            <div class="desc-text-left">
              <p
                class="desc-text-title bold-font margin-top-20 margin-bottom-30 font-size24"
              >健康问卷“巧”优化，“新”系客户助承保</p>
            </div>
          </div>
          <!--pc-->
          <div class="nps-story-p"  >
            <div class="desc-example-wrapbox ">
              <div class="desc-example-leftbox">
                <div class="customer-flexbox-decor ">
                  <img
                    src="http://mslpop3.oss-cn-hangzhou.aliyuncs.com/static/Branding/2020/ykwx/assets/quotes_left@3x.png"
                    alt
                  />
                  <div class="box-border-empty"></div>
                  <div class="box-border-line"></div>
                </div>
                <div class="customer-flex-bottom margin-bottom-30" style="margin-top:10px">
                  <p
                    class="font-size16 color282B3E "
                  >新契约部自2019年7月启动净推荐体系以来，始终坚持以“ <b>客户心声</b> ”为基础进行各项业务的开展与流程的优化。<b>电子签收，飞签，电子照会</b>，
                  微信进行犹豫期回访…多项投保流程相关的创新措施，背后无一不来自于客户的需求与希望。而如何从成百上千的客户反馈中发现改善的空间，甚至新的业务发展方向，
                  无疑是该团队每一位成员时刻“<b>心系客户</b>”，站在客户角度不断分析讨论与不断实践的成果，同时也是公司服务理念进一步深化，净推荐体系“<b>聆听、学习、行动</b>”工作机制行之有效的有力体现。
                  <br>
                  <br>
                  小组例会中，核保员们首先共同<b>回顾了该客户保单的核保记录</b>。原来，该客户在两个健康告知问题中有异常告知，但告知内容不完整不清楚，
                  使得核保员无法根据客户告知的内容作出准确的判断，因此下发照会请客户具体说明，且该保单在客户回复照会后已正常承保。<b>还原案例经过</b>后，
                  大家回到客户的角度，放下专业的核保知识，设想首次投保的客户会怎样理解健康告知问卷，怎样才能让客户提供必要且清晰的健康告知。
                  </p>
                </div>
              </div>
              <div style="width:55%"><img src="http://mslpop3.oss-cn-hangzhou.aliyuncs.com/static/Branding/2020/ykwx/assets/servise_pic1@2x.png" alt=""></div>
            </div>

            <div style="display:flex">
              <div><img style="width:132px;height:auto;margin-right:20px;" src="http://mslpop3.oss-cn-hangzhou.aliyuncs.com/static/Branding/2020/ykwx/assets/servise_pic2@3x.png" alt=""></div>
              <div style="width:92%;font-size:16px;line-height: 25px;">
                通常，我们会<b>更关注推荐者与贬誉者的反馈</b>，而9月的NPS调研中，一位中立者的评价却引起了团队成员的注意。该客户虽然对于<b>公司推荐度给予了较高的8分</b>，
                但对于投保过程中<b>递交补充资料这一环节表达了不满</b>，认为公司未能在第一时间明确告知其需要补充的资料，导致了递交的反复，并<b>对下发照会的原因表示不解</b>。
                照会是核保团队与客户沟通的主要方式，若照会内容难以理解或容易对客户造成误解，将大大降低核保线上通过率以及客户投保体验的流畅程度。因此，新契约部决定以此案例为契机，
                <b>再次对健康告知问卷进行审阅</b>。
                <br><br>
                热烈讨论后，核保员们一致认为可以<b>在健康告知问卷的问题设计上花点“巧思”</b>：例如针对询问客户五年内是否有做过某项检查这类问题，客户会直接回答做过检查但常常不告知检查结果；
                若修改为询问客户是否做过某些检查且结果异常，这样一来既提醒了客户将异常情况告知，免去核保员再发照会询问，从而使核保员更快速直接地作出判断，对于检查结果正常的客户也可直接进行线上核保，无需人工介入。
                再比如针对一年内体重是否有5公斤以上的增减这一问题，一年内有过怀孕分娩的女性客户常常会因为没有说明原因而收到照会。若在题目中直接列明（怀孕分娩除外）将有效避免此类客户收到照会……
                <br><br>
                目前，相关的优化需求已提交，相信对告知问题进行更加细致的设计后，我们的客户<b>能更精准地告知异常健康情况</b>以便核保员进行判断，同时客户也<b>能获得更顺畅更优质的投保体验</b>。
              </div>
            </div>
            
            <div class="customer-flexbox-decor">
                  <div class="box-border-line"></div>
                  <div class="box-border-empty"></div>
                  <img
                    src="http://mslpop3.oss-cn-hangzhou.aliyuncs.com/static/Branding/2020/ykwx/assets/quotes_right@3x.png"
                    alt
                  />
            </div>
          </div>
           <!--mb-->
          <div class="nps-story-m">
            
            <div class="customer-flexbox-decor ">
              <img
                    src="http://mslpop3.oss-cn-hangzhou.aliyuncs.com/static/Branding/2020/ykwx/assets/quotes_left@3x.png"
                    alt
              />
